.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

body {
  font-family: 'Poppins',sans-serif;
  position: relative;
}

body, html {
    height: 100%;
    margin: 0;
}

.contentContainer {
  margin-right: 5%;
  margin-left: 5%;
}

.panel-body {
  background-color:black !important;
}

::selection {
  background: #333333;
  color: white;
}
